section {
  grid-column-start: -4;
  grid-column-end: -1;
  order: 4;
  margin-top: 3vw;
}
.clavier {
  display: flex;
  justify-content: center;
  position: relative;
  width: auto;
  margin: auto;
  transition: all 1s ease;
  backface-visibility: hidden;
  border-top: 10px solid black;
  border-left: 10px solid black;
  border-right: 10px solid black;
}

.hidden {
  transform: rotateY(180deg);
}

.clavier ul {
  list-style: none;
  display: flex;
  height: auto;
  border: 1px solid black;
}

.clavier ul li {
  position: relative;
  background-color: white;
  border: 1px solid black;
  width: 60px;
  height: 250px;
}

.clavier ul p.black {
  background-color: black;
  height: 180px;
  position: absolute;
  width: 30px;
  transform: translateX(150%);
  z-index: 100;
  box-shadow: 1px 1px 1px black;
  transition: box-shadow 0.3s ease;
}

.clavier ul p.black:active {
  box-shadow: none;
}

.white {
  transition: all 0.1s ease;
}

.white:active {
  box-shadow: inset 1px 0px 3px 0px black;
}

.hide {
  padding: 5px;
  cursor: pointer;
  margin: 10px;
}

li.white.accord-active {
  background-color: darkred;
  box-shadow: inset 1px 0px 3px 0px black;
}

ul li p.black.accord-active {
  background-color: darkred;
  box-shadow: none;
}

@media screen and (max-width: 1275px) {
  .clavier ul li {
    width: 50px;
    height: 200px;
  }

  .clavier ul li p.black {
    height: 150px;
    width: 25px;
    transform: translateX(150%);
  }
}
@media screen and (max-width: 1060px) {
  .clavier ul li {
    width: 30px;
    height: 150px;
  }

  .clavier ul li p.black {
    height: 110px;
    width: 15px;
    transform: translateX(150%);
  }
}
@media screen and (max-width: 640px) {
  .clavier ul li {
    width: 20px;
    height: 100px;
  }

  .clavier ul li p.black {
    height: 80px;
    width: 10px;
    transform: translateX(150%);
  }
}

@media screen and (max-width: 430px) {
  .clavier .c2 {
    display: none;
  }
}
