h1 {
  text-align: center;
  font-size: clamp(25px, 5vw, 60px);
  margin: 20px;
  font-family: "Libre Baskerville", serif;
}

main {
  margin: auto;
  width: 80%;
  padding: 50px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;
  align-items: center;
  text-align: center;
}

@media screen and (max-width: 300px) {
  main {
    padding: 0;
    width: 95%;
  }
}

/*/////////////////////////////////////// selectNote /////////////////////////////////////*/

.select {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(4, 47px);
  grid-template-rows: repeat(4, 40px);
  grid-gap: 2px;
  justify-content: center;
  order: 1;
}

button.rand {
  grid-column-start: 1;
  grid-column-end: 5;
}

.select button {
  border: 1px solid black;
  border-radius: 5px;
  padding: 10px;
  text-align: center;
  cursor: pointer;
  transition: all 0.1s ease-out;
}

.select button:hover {
  background-color: #f1f1f1;
  transform: scale(1.1);
}

.select button:nth-child(2),
.select button:nth-child(4),
.select button:nth-child(7),
.select button:nth-child(9),
.select button:nth-child(11) {
  background-color: black;
  color: white;
}

.select button:nth-child(2):hover,
.select button:nth-child(4):hover,
.select button:nth-child(7):hover,
.select button:nth-child(9):hover,
.select button:nth-child(11):hover {
  background-color: #353535;
}

button.act {
  font-weight: 800;
  transform: scale(1.1);
}
@media screen and (max-width: 720px) {
  .select button {
    padding: 0;
  }
  .select {
    grid-gap: 1px;
    grid-auto-flow: column;
    grid-template-columns: repeat(3, 30px);
    grid-template-rows: repeat(4, 20px);
    display: flex;
    flex-wrap: wrap;
  }

  main {
    display: block;
  }
}
