.bpm {
  font-size: clamp(16px, 1.5vw, 30px);
  order: 3;
}

.more,
.less {
  font-size: 25px;
  width: 30px;
  height: 30px;
  cursor: pointer;
  border: none;
  background-color: #fff;
}

.toggle-btn {
  border: 1px solid black;
  padding: 10px;
  border-radius: 5px;
  height: fit-content;
  cursor: pointer;
}

.no-wrap {
  white-space: nowrap;
}
